<template>
    <div>
        <div class="boot-page">
            <p><span>亲属关系企业</span><u class="clear" @click="clear">清空列表</u></p>
            <div id="sea-content">
                <dl class="dl" v-for="(search,idx) in searchList">
                    <dt>
                        <input placeholder="请输入人物姓名" v-model="search.value" class="reduce" type="text" @keyup="keyupStop(idx)" :disabled="search.usable"><strong><i v-if="idx!==searchList.length-1" :class="['iconfont','icon-reducebg01',disable]" @click="del(idx)"></i>
                        <i v-if="idx==searchList.length-1" :class="['iconfont','icon-add',search.disable]" @click="add(search)"></i></strong>
                    </dt>
                    <dd v-if="search.show" @click="changeMsk(idx)">
                              能找到<span>{{search.count}}</span>个<i>{{search.value}}</i>，
                              <u class="filter">点击筛选</u>结果更精彩
                    </dd>
                </dl>
            </div>
                <div class="btn-analysis" @click="routerChange">
                        关联分析
                </div>
        </div>
        <div class="main-content" v-show="showContent">
            <span class="link-box" @click="showExample">
                <img src="../../assets/img/spr-contact.png">
            </span>
            <div class="img-box">
                <span class="img-item">
                    <span class="title">挖掘人之间关系</span>
                </span>
                <span class="img-item">
                   <span class="title">预警潜在风险</span>
                </span>
            </div>
        </div>
        <showInfo v-show="!showContent" :administrationName="administrationName"></showInfo>
    </div>
</template>
<script>
    import '../../assets/font/iconfont.css'
    import showInfo from  './showInfo.vue'
    export default {
        name: 'content',
        data(){
            return {
                showContent:true,
                clickIndex:0,
                keyupInterval:-1,
                disable:'disable',
                searchList:[
                    {value:'',count:0,show:false,disable:'disable',usable:false},
                    {value:'',count:0,show:false,disable:'disable',usable:true}
                ],
                routerOk:false,
                rou_Change:true,
                obj:{
                    msk:"",
                    text:"",
                },
                obj2:{
                    "tar":true,
                    "cur":0
                },
                big:"",
                administrationName:""
            }
        },
        methods:{
            keyupStop(idx){
                var analysis=document.querySelector(".btn-analysis");
                let totalLen=this.searchList.length;
                let len=this.searchList[idx].value.length;
                if(idx>=1 && len>=2){
                    if(totalLen<4){
                        this.searchList[idx].disable = '';    //  加按钮变为可用状态
                        analysis.classList.add("btn-analysis-eff");
                    }
                    this.routerOk=true;
                }else{
                    if(idx<1 && len>2){
                        if(this.searchList[idx+1].value.length>2){
                            analysis.classList.add("btn-analysis-eff");
                            this.routerOk=true;
                        }

                    }else{
                        analysis.classList.remove("btn-analysis-eff")
                    }
                    this.searchList[idx].disable = 'disable';
                }
                if(len>=2){
                    if(totalLen>idx+1){
                        this.searchList[idx+1].usable=false;
                    }
                    this.keyupInterval++;       //   定时器启动一次标志
                    if(!this.keyupInterval){
                        var _id=setTimeout(function(){
                            this.keyupInterval=-1;
                            clearTimeout(_id);
                            this.queryCount(idx);
                        }.bind(this),1000)
                    }
                }else{
                    if(totalLen>idx+1){
                        this.searchList[idx+1].usable=true;
                    }
                    this.searchList[idx].show = false;
                    this.searchList[idx].count = 0;
                }
            },
            showExample(){
                this.obj2.cur++;
                app.exm(this.obj2);
            },
            routerChange(){
                if(this.routerOk==true){
                    this.showContent=false;
                    this.administrationName="";
                    for(var i=0;i<this.searchList.length;i++){
                        this.administrationName=this.administrationName+this.searchList[i].value+",";
                    }
                    app.administrationName(this.administrationName)
                    this.rou_Change=false;
                }
            },
            clear(){
                this.searchList = [
                    {value:'',count:0,show:false},
                    {value:'',count:0,show:false}
                ];
                this.$router.push('/');
            },
            queryCount(idx){
                this.searchList[idx].show = true;
                this.searchList[idx].count = 200;
            },
            changeMsk(index){
                this.clickIndex=index;
                this.obj.msk=true;
                this.obj.text=this.searchList[index].value;
                this.$emit("getMsk",this.obj)
            },
            add(input){
                let len=input.value.length;
                if((this.searchList.length>=3) || input.disable.indexOf("disable")!=-1) return;
                if(len>=2){
                    this.searchList.push({
                        value:'',count:0,show:false,disable:'disable',usable:false
                    });
                }else{
                    this.searchList.push({
                        value:'',count:0,show:false,disable:'disable',usable:true
                    });
                }
                this.disable = '';     //  减按钮可用
            },
            del(idx){
                if(this.searchList.length<=2) return;
                this.searchList.splice(idx,1);
                if(this.searchList.length<=2){
                    this.disable = "disable";
                }
            }
        },
        props:["text"],
        components:{
            showInfo,
        },
        watch: {
            text(newValue, oldValue) {
                this.searchList[0].value=this.text;
//                if (!newValue) {
//                    this.searchList[this.clickIndex].value=this.text;
//                }
            },
        }
    }
</script>
<style lang="scss" scoped>
    @import "./../../assets/css/_variable.scss";
    @import "./../../assets/css/_mixin.scss";
    .boot-page{
        .dl{
            input{
                box-sizing: border-box;
                width: 212px;
                height: 34px;
                background:#091523;
                border:1px solid transparent;
                outline:none;
                color:#fff;
                text-indent:10px;
            }
            input:focus{
                outline:none;
                border:1px solid #094663;
                box-shadow:0 0 5px rgba(20,91,120,1.15); //向外漫射程度1
                -webkit-box-shadow:0 0 8px rgba(20,91,120,1.15);//向外漫射程度3
            }
            strong{
                display:inline-block;
                width:16px;
                height:16px;
                border-radius: 50%;
                background-color: #e3e5e7;
                line-height:38px;
                margin-left:15px;
                position: relative;

            }
            i{
                color:#f0490e;
            }
            dt{
                padding:10px 0;
                i{
                    position: absolute;
                    top:-8px;
                    left:-5px;
                    color:#0089fe;
                    font-size:26px;
                    cursor:pointer;
                }
                i:hover{
                    color:#007ae6;
                }
                .disable{
                    color:#155698;
                }
                .disable:hover{
                    color:#155698;
                    cursor: default;
                }
            }
            dd{
                color:#a9b0b6;
            }
        }
        .searchDetial{
            position: absolute;
            left:50%;
            top:50%;
            transform:translate(-50%,-50%);
            z-index:2;
        }
        #sea-content{
            font-size:12px;
            .filter{
                padding:0 4px;
                cursor: pointer;
                color:#fff;
            }
            .filter:hover{
                color:#0493ca;
            }
        }
        box-sizing: border-box;
        width: 290px;
        height: 820px;
        background-color:rgba(7,20,32,0.5);
        float:left;
        padding:20px;
        p{
            padding-bottom:24px;
        }
        span{
            float:left;
            font-weight:bold;
        }
        .clear{
            font-size:$fz-xs;
            float:right;
            cursor: pointer;
        }
        .clear:hover{
            color:#03e4ff;
        }
        .btn-analysis{
            margin-top:20px;
            width:100%;
            height:36px;
            border-radius:5px;
            outline:none;
            font-size: 14px;
            color:#03e4ff;
            font-weight:bolder;
            text-align:center;
            line-height:36px;
            border:1px solid #045782;
            background-color: rgba(33,74,101,0.6);
        }
        .btn-analysis-eff{
            background: linear-gradient(to top,rgba(12,110,197,0.5),rgba(19,100,166,0.5),rgba(15,69,116,0.5));
            background: -webkit-linear-gradient(to top,rgba(12,110,197,0.5),rgba(19,100,166,0.5),rgba(15,69,116,0.5));
            cursor: pointer;
        }
        .btn-analysis-eff:hover{
            color:#03e4ff;
            background: linear-gradient(to top,rgba(5,91,154,0.6),rgba(7,70,122,0.6),rgba(10,50,89,0.6));
            background: -webkit-linear-gradient(to top,rgba(5,91,154,0.6),rgba(7,70,122,0.6),rgba(10,50,89,0.6));
        }
    }
    .link-box{
        display: inline-block;
        img{
            width: 64px;
            height: 64px;
            border-radius: 50%;
        }
        img:hover{
            cursor: pointer;
            box-shadow:0 0 14px rgba(52,225,255,1.15); //向外漫射程度1
        }
    }
    .main-content{
        overflow: hidden;
        position:relative;
        width:900px;
        height:820px;
        background:rgba(7,20,32,0.5);
        float:right;
        span.link-box{
            position: absolute;
            top:30px;
            right:90px;
            cursor: pointer;
            img:hover{
                border-radius: 50%;
                box-shadow:0 0 24px #2afefb ;
            }
        }
        .img-box{
            width: 100%;
            height: 267px;
            margin: 24% auto 0 auto;
            text-align: center;
        }
        .img-box .img-item{
            display: inline-block;
            width: 344px;
            height: 277px;
            margin: 0 3%;
            background-position: center center;
            background-size: cover;
        }
        .img-box .img-item:first-child{
            background-image:url(../../assets/img/spr-excavate.png);
        }
        .img-box .img-item:last-child{
            background-image:url(../../assets/img/spr-potential.png);
        }
        .img-box .title{
            display: inline-block;
            position: relative;
            top: 286px;
            width: 100%;
            text-align: center;
            font-size: 14px;
            color: #00eaff;
        }
    }
</style>


