<template>
    <div class="paging">
    <ul>
      <li>
        <a class="first" @click="goto(1)">跳转到首页</a>
      </li>
      <li>
        <a @click="goto(pageIndex-1)" title="上一页">上一页</a>
      </li>

      <li v-for="n in pages" :key="n">
        <a :class="{active:n==pageIndex}" @click="goto(n)">{{n}}</a>
      </li>
      <li>
        <a @click="goto(pageIndex+1)">
          下一页
        </a>
      </li>
      <li>
        <a class="last" @click="goto(totalPage)">
          跳转到尾页
        </a>
      </li>
      <li v-if="mode=='full'" class="skip">
        到第
        <input type="text" v-model="skipNum" @keyup.enter="goto(skipNum)" />页
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    initPageSize: {
      type: Number,
      default: 10
    },
    initPageIndex: {
      type: Number,
      default: 1
    },
    initShowPages: {
      type: Number,
      default: 5
    },
    initTotalCount: {
      type: Number,
      default: 0
    },
    initMode: {
      type: String,
      default: 'full'
    }
  },
  data() {
    return {
      pageSize: this.initPageSize,
      pageIndex: this.initPageIndex,
      showPages: this.initShowPages,
      mode: this.initMode,
      totalPage: 0,
      pages: [],
      skipNum: ''
    }
  },
  watch: {
    initTotalCount(curVal) {
      this.totalCount = curVal

      this.showPages = this.initShowPages// 变更初始总页数后,重置showPages、mode,避免造成...丢失
      this.reset()
      this.getPages()
    },
    initPageIndex: function (curVal) {
      if (this.pageIndex !== curVal) {
        this.pageIndex = curVal
        this.getPages()
      }
    }
  },
  methods: {
    goto: function (page) {
      page = +page
      if (isNaN(page)) return
      if (page < 1 || page > this.totalPage || page === this.pageIndex) return;
      this.pageIndex = page
      this.getPages()
      this.$emit('pageChange',this.pageIndex)
    },
    reset: function () {
      this.totalPage = Math.ceil(this.totalCount / this.pageSize)
      if (this.totalPage <= 1) return
      if (this.showPages >= this.totalPage) {
        this.mode = 'simple'
        this.showPages = this.totalPage
      } else {
        this.mode = 'full'
      }
    },
    getPages: function () {
      let pages = []
      if (this.totalPage > 1) {
        let half = Math.floor(this.showPages / 2)
        let end = Math.min(this.totalPage, this.pageIndex + half)
        let start = Math.max(1, end - this.showPages + 1)
        end = Math.min(this.totalPage, start + this.showPages - 1)
        for (let pi = start; pi <= end; pi++) {
          pages.push(pi)
        }

        if (this.mode === 'full') {
          let diff = this.totalPage - end
          if (diff > 2) {
            pages.push('...', this.totalPage)
          } else {
            for (let i = 1; i <= diff; i++) {
              pages.push(end + i)
            }
            for (let i = -1; i >= diff - 2; i--) {
              if (start + i > 0) { // total为6时,出现0页
                pages.unshift(start + i)
              }
            }
          }
        }
      }

      this.pages = pages
    }
  },
  mounted() {
    this.totalCount = this.initTotalCount// 跳转路由后,重新赋值,否则为undefined
    this.reset()
    this.getPages()
  },
}
</script>
<style lang="scss" scoped>
  .paging{
    position: absolute;
    bottom: 20px;
    right:20px;
    li{
      float: left;
    }
    input{
      width:30px;
      margin:0 5px;
    }
    a{
      color:#fff;
      padding:2px 5px;
      cursor: pointer;
    }
    a:hover{
      color:#00affd;
    }
  }
</style>



