<template>
    <div id="exampleList">
        <ul>
            <li v-for="(item ,index) in actives" :class="{ show:index===now }">
                <span class="change-base" :class='styleTypes[index]' @click="next1(index)"></span>
                <img :src="item.imgUrl" alt="">
            </li>
        </ul>
    </div>
</template>
<style lang="scss" scoped>
  #exampleList{
            position: absolute;
            left:50%;
            top:50%;
            transform:translate(-50%,-50%);
            z-index:2;
            li{
                display:none;
                position: relative;
            }
            .show{
                display:block;
            }
            .change-base{
                position: absolute;
                z-index:3;
                display:inline-block;
                cursor: pointer;
            }
            .change{
                left:36px;
                top:70px;
                width: 130px;
                height: 140px;
            }
            .change1{
                left:96px;
                top:230px;
                width: 580px;
                height: 370px;
            }
            .change2{
                left:146px;
                top:270px;
                width: 130px;
                height: 140px;
            }
            .change3{
                left:96px;
                top:140px;
                width: 550px;
                height: 520px;
            }
   }

</style>
<script>
    import active1 from '../../assets/img/active/spr-slide4.jpg'
    import active2 from '../../assets/img/active/spr-slide5.jpg'
    import active3 from '../../assets/img/active/spr-slide2.jpg'
    import active4 from '../../assets/img/active/spr-slide3.jpg'
    import active5 from '../../assets/img/active/spr-slide1.jpg'
    export default{
        name:"exampleList",
       data(){
           return {
               now:0,
               styleTypes: [
                   'change',
                   'change1',
                   'change2',
                   'change3',
                   'change4',
               ],
               actives:[
                   {imgUrl:active1},
                   {imgUrl:active2},
                   {imgUrl:active3},
                   {imgUrl:active4},
                   {imgUrl:active5},
               ]
           }
       },
        methods:{
            next1(index){
                this.now++;
                if(this.now>=5){
                    this.now=0;
                }
            }
        },
        props:["current"],
        watch:{
            current: function(newValue, oldValue) {
                if(newValue!=oldValue){
                    this.now=0;
                }
            }
        }
    }
</script>


