<template>
    <div id="showInfo">
        <div class="big-change">
                <dl @click="bigChange(0)">
                    <dt>
                        <img src="../../assets/img/icon-info.png" alt="">
                    </dt>
                    <dd>
                        <p class="sys"><span class="litigation-info">诉讼数量</span><em class="litigation-count">{{litigationData.litigationTotal}}</em></p>
                        <p class="shy"><span>平均每家诉讼</span><em>{{litigationData.litigationAverage}}</em></p>
                    </dd>
                </dl>
                <dl @click="bigChange(1)">
                <dt>
                    <img src="../../assets/img/icon-info1.png" alt="">
                </dt>
                <dd>
                    <p class="sys"><span class="litigation-info">失信数量</span><em class="litigation-count">{{dishonestyData.dishonestyTotal}}</em></p>
                    <p class="shy"><span>平均失信数量</span><em>{{dishonestyData.dishonestyAverage}}</em></p>
                </dd>
            </dl>
                <dl @click="bigChange(2)">
                <dt>
                    <img src="../../assets/img/icon-info2.png" alt="">
                </dt>
                <dd>
                    <p class="sys"><span class="litigation-info">行政处罚数</span><em class="litigation-count">{{punishData.punishTotal}}</em></p>
                    <p class="shy"><span>平均每家处罚</span><em>{{punishData.punishAverage}}</em></p>
                </dd>
            </dl>
                <dl @click="bigChange(3)">
                <dt>
                    <img src="../../assets/img/icon-font3.png" alt="">
                </dt>
                <dd>
                    <p class="sys"><span class="litigation-info">变更风险数</span><em class="litigation-count">{{changeData.changeTotal}}</em></p>
                    <p class="shy"><span>平均每家变更</span><em>{{changeData.changeAverage}}</em></p>
                </dd>
            </dl>
                <dl @click="bigChange(4)">
                <dt>
                    <img src="../../assets/img/icon-info4.png" alt="">
                </dt>
                <dd>
                    <p class="sys"><span class="litigation-info">互联网负面</span><em class="litigation-count">{{negativeData.negativeTotal}}</em></p>
                    <p class="shy"><span>平均每家负面</span><em>{{negativeData.negativeAverage}}</em></p>
                </dd>
            </dl>
        </div>
        <div class="relationGraph">
            <div class="show">
                <button class="only-show">仅展示关联企业</button>
                <button class="all-show">展示全部企业</button>
            </div>
            <div class="tubiao">
                <div id="tubiao" style="height:700px;width:600px"></div>
            </div>
        </div>
        <div class="info">
            <div class="example" v-for="i in enterpriseList">
                <p v-text="i.enName"></p>
                <ul>
                    <li><span>诉讼数量:</span><i v-text="i.litigation"></i></li>
                    <li><span>失信数量:</span><i v-text="i.dishonesty"></i></li>
                    <li><span>行政处罚数量:</span><i v-text="i.punish"></i></li>
                    <li><span>变更风险数量:</span><i v-text="i.change"></i></li>
                    <li><span>互联网负面数量:</span><i v-text="i.negative"></i></li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
import '../../assets/css/showinfo.scss'
import echarts from "echarts/lib/echarts"
import "echarts/lib/chart/graph"
import "echarts/lib/component/tooltip"
import "echarts/lib/component/legend"
import axios from "axios"
export default {
    name: "showInfo",
    data() {
        return {
            conditionSmall: false,
            conditionBig: false,
            start_ok: false,
             litigationData: {litigationTotal: 100, "litigationAverage": 20},
             "dishonestyData": {"dishonestyTotal": 100, "dishonestyAverage": 20},
             "punishData": {"punishTotal": 100, "punishAverage": 20},
             "changeData": {"changeTotal": 100, "changeAverage": 20},
             "negativeData": {"negativeTotal": 100, "negativeAverage": 20},
            "enterpriseList": [
                {
                    "enName": "xx公司",
                    "litigation": "无诉讼记录",
                    "dishonesty": 2,
                    "punish": 3,
                    "change": 5,
                    "negative": 7
                },
                {
                    "enName": "xx公司",
                    "litigation": 3,
                    "dishonesty": 2,
                    "punish": "无处罚记录",
                    "change": 5,
                    "negative": 7
                },
                {
                    "enName": "xx公司",
                    "litigation": "无诉讼记录",
                    "dishonesty": 2,
                    "punish": 3,
                    "change": 5,
                    "negative": 7
                }
            ],
            list:[],
            "copyData":[
                {
                    "name":"张三",
                    "data":[
                        {"enName":"公司1","outport":1,"position":"总裁"},
                        {"enName":"公司2","outport":1,"position":"经理"},
                        {"enName":"公司3","outport":0,"position":"总裁"}
                    ]
                },
                {
                    "name":"李四",
                    "data":[
                        {"enName":"公司6","outport":1,"position":"总裁"},
                        {"enName":"公司7","outport":1,"position":"总裁"},
                        {"enName":"公司4","outport":0,"position":"经理"}
                    ]
                }
            ],
            "node":[
                {
                    "name": "张三", "x": 200, "y": 60,
                    "symbolSize": 65,"category":2,
                    "color":"#41a0fe"
                },
                {
                    "name": "公司7", "x": 200, "y": 60,
                    "symbolSize": 72,"category":1,
                    "color":"#f86b4d"
                },
                {
                    "name": "公司8", "x": 200, "y": 60,
                    "symbolSize": 54,"category":1,
                    "color":"#f86b4d"
                },
                {
                    "name": "公司9", "x": 200, "y": 60,
                    "symbolSize": 54,"category":1,
                    "color":"#f86b4d"
                },


                {
                    "name": "无机酸\n制造", "x": 200, "y": 60,
                    "symbolSize": 63,"category":2,
                    "color":"#41a0fe"
                },
                {
                    "name": "公司4", "x": 200, "y": 60,
                    "symbolSize": 72,"category":0,
                    "color":"#06377c"
                },
                {
                    "name": "公司5", "x": 200, "y": 60,
                    "symbolSize": 54,"category":0,
                    "color":"#06377c"
                },
                {
                    "name": "公司6", "x": 200, "y": 60,
                    "symbolSize": 54,"category":0,
                    "color":"#06377c"
                },
                {
                    "name":"类型\n企业3", "x": 200, "y": 60,
                    "symbolSize": 68,"category":2,
                    "color":"#41a0fe"
                },
                {
                    "name": "公司1", "x": 200, "y": 60,
                    "symbolSize": 72,"category":1,
                    "color":"#f86b4d"
                },
                {
                    "name": "公司2", "x": 200, "y": 60,
                    "symbolSize": 54,"category":0,
                    "color":"#06377c"
                },
                {
                    "name": "公司3", "x": 200, "y": 60,
                    "symbolSize": 54,"category":0,
                    "color":"#06377c"
                },
                {
                    "name": "", "x": 330, "y": 60,
                    "symbolSize":0,
                }
            ],
            "nodeList":[
                {"source": "公司7", "target": "张三"},
                {"source": "公司8", "target": "张三"},
                {"source": "公司9", "target":"张三"},
                {"source": "公司9", "target": "公司4"},


                {"source": "公司1", "target": "类型\n企业3"},
                {"source": "公司1", "target": "公司6"},
                {"source": "公司2", "target": "类型\n企业3"},
                {"source": "公司2", "target": "公司8"},
                {"source": "公司3", "target": "类型\n企业3"},

                {"source": "公司4", "target": "无机酸\n制造"},
                {"source": "公司4", "target": "公司6"},
                {"source": "公司5", "target": "无机酸\n制造"},
                {"source": "公司6", "target": "无机酸\n制造"}
            ],
            obj1: {
                big: 0,
                index: ""
            }
        }
    },
    props:["administrationName"],
    mounted(){
        this.initPage();
        this.getUrl("http://192.168.1.68:8069/kinship/enterprise/analysisData?a=1","http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/kinship/enterprise/enterpriseList",{
            params:{
                administrationName:this.administrationName,
            }
        })
    },
    methods: {
        bigChange(index) {
            this.obj1.index = index;
            app.whichSlot(this.obj1);
        },
        initPage(){
            let _this = this;
            this.getJson({
                params:{
                    administrationName:this.administrationName,
                }
            })
            this.addData();
        },
        initChart(arr1, arr){
            var myChart = echarts.init(document.getElementById('tubiao'));
            myChart.setOption({
                tooltip: {
                    show:false,
                    trigger: 'item',
                },
                legend: {
                    itemWidth:14,
                    itemHeight:14,
                    borderColor:"#fff",
                    x: 'right',
                    data:  [
                        {
                            name:'企业',
                            icon: 'circle'
                        },
                        {
                            name:'风险企业',
                            icon: 'circle'
                        },
                        {
                            name:'企业高管名称',
                            icon: 'circle'
                        }
                    ],
                    textStyle:{ color:"#fff"},
                    inactiveColor:"#fff"
                },
                series: [{
                        type: 'graph',
                        layout: 'force',
                        edgeSymbol: ['arrow'],
                        categories: [
                            {
                                name: '企业'
                            },
                            {
                                name: '风险企业'
                            },
                            {
                                name: '企业高管名称'
                            }
                        ],
                        itemStyle:{
                            normal:{
                                shadowColor:"rgba(255,255,255,0.4)",
                                shadowBlur:"30"
                            }
                      },
                        force: {
                            gravity: 0.08,
                            edgeLength: 8,
                            repulsion: 50
                        },
                        grid: {
                            top: '15%',
                            left: '1%',
                            right: '1%',
                            bottom: '15%',
                            containLabel: true
                        },

                        symbolSize: 64,
                        label: {
                            normal: {
                                show: true,
                                textStyle: {
                                    fontSize: 16,
                                    color: '#fff',
                                }
                            }
                        },
                        lineStyle: {
                            normal: {
                                color: '#52a2a3',
                                curveness: 0.2,
                                width: 2
                            }
                        },
                    data:arr,
                    links:arr1,
                    }
                ]
            });
            myChart.on('click', function (params) {
                if (params.componentType === 'markPoint') {
                    // 点击到了 markPoint 上
                }
                else if (params.componentType === 'series') {
                    if (params.seriesType === 'graph') {
                        if(params.data.category==0 || params.data.category==1){
                            app.comDetial(true);
                        }
                    }
                }

            });
        },
        getJson(params){
            let that=this;
            axios.get("http://192.168.1.68:8069/kinship/enterprise/analysisData?a=2",params)
                .then(function(res){
                    if(res.data.code==501){
                        axios.get("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/kinship/enterprise/analysisData",params)
                            .then(function(res){
                                let data=res.data.data;
                                that.litigationData=data.litigationData;
                                that.dishonestyData=data.dishonestyData;
                                that.punishData=data.punishData;
                                that.changeData=data.changeData;
                                that.negativeData=data.negativeData;
                            }).catch(function(res){
                            console.log("data error")
                        })

                    }else{
                        let data=res.data.data;
                        that.litigationData=data.litigationData;
                        that.dishonestyData=data.dishonestyData;
                        that.punishData=data.punishData;
                        that.changeData=data.changeData;
                        that.negativeData=data.negativeData;
                    }
                }).catch(function(res){
                console.log("data error")
            })
        },
        getData(params){
            let that=this;
            axios.get("http://192.168.1.68:8069/kinship/enterprise/analysisData?a=2",params)
                .then(function(res){
                    if(res.data.code==501){
                        axios.get("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/kinship/enterprise/analysisData",params)
                            .then(function(res){
                                let data=res.data.data;
                                that.litigationData=data.litigationData;
                                that.dishonestyData=data.dishonestyData;
                                that.punishData=data.punishData;
                                that.changeData=data.changeData;
                                that.negativeData=data.negativeData;
                            }).catch(function(res){
                            console.log("data error")
                        })

                    }else{
                        let data=res.data.data;
                        that.litigationData=data.litigationData;
                        that.dishonestyData=data.dishonestyData;
                        that.punishData=data.punishData;
                        that.changeData=data.changeData;
                        that.negativeData=data.negativeData;
                    }
                }).catch(function(res){
                console.log("data error")
            })
        },
//        getJson(){
//            let that=this;
//            //   请求右侧所有公司详情mock数据
//            axios.get("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/kinship/enterprise/enterpriseList",{
//                params:{
//                    administrationName:this.administrationName,
//                }
//            })
//                .then(function(res){
//                    that.enterpriseList=res.data.data.enterpriseList;
//                }).catch(function(res){
//                console.log("data error")
//            })
//
//            //   请求ecahrt图标的相关数据
//
//        },
        addData(){
            let len=this.copyData.length;
            let arr=[];
            let arr1=[];
            let sonLen;
            for(var i=0;i<len;i++){
                let $t=this.copyData[i];
                let obj= {
                    "name":"aa", "x": 200, "y": 60,
                    "symbolSize": 54,"category":2,
                    "itemStyle": {
                        "normal": {
                            "color": "#41a0fe"
                        }
                    }
                };
                obj.name=$t.name;
                sonLen=$t.data.length;
                for(var m=0;m<sonLen;m++){
                    let obj= {
                        "name":"aa", "x": 200, "y": 60,
                        "symbolSize": 54,"category":1,
                        "itemStyle": {
                            "normal": {
                                "color": "#41a0fe"
                            }
                        }
                    };
                    let obj1={"source": "公司7", "target": "张三"};
                    obj1.source=$t.data[m].enName;
                    obj1.target=$t.name;
                    obj.name=$t.data[m].enName;
                    if($t.data[m].outport==1){
                        obj.category=1;      //   危险企业
                        obj.itemStyle.normal.color="#f86b4d"
                    }else{
                        obj.category=0;
                        obj.itemStyle.normal.color="#06377c";
                    }
                    arr.push(obj)
                    arr1.push(obj1)
                }
                arr.push(obj)
                arr.push({
                    "name": "", "x": 330, "y": 60,
                    "symbolSize":0,
                })
            }
            this.initChart(arr1, arr);

        }
    }
}
</script>




