<template>
    <div id="partDetial">
        <div class="sro">
            <slot :name="item.name" v-for="item in items">
            </slot>
       </div>
    </div>
</template>
<script>
    export default{
        data(){
            return {
                items:[
                    {name:"part1",index:0},
                    {name:"part2",index:1},
                    {name:"part3",index:2},
                    {name:"part4",index:3},
                    {name:"part5",index:4}
                ],
            }
        }
    }
</script>


