import Vue from 'vue'
import Router from 'vue-router'
import showInfo from '../pages/boot-page/showInfo.vue'
import defaultMain from '../pages/boot-page/defaultMain.vue'
Vue.use(Router);
var router=new Router({
    routes: [
        {
            path: '/showInfo',
            name: 'showInfo',
            component: showInfo,
        },
        {
            path: '',
            name: '',
            component: defaultMain
        }
    ]
});
export default router;



