<template>
    <div id="selectedCity">
        <p class="title">选择区域/园区</p>
        <div class="area-content">
            <ul class="selectedArea">
                <li class="active">全国</li>
                <li @click="changePro">{{sendJson.lastProvince}}</li>
                <li @click="changeCit">{{sendJson.lastCity}}</li>
                <li>{{sendJson.lastDistrict}}</li>
            </ul>
            <ol class="area-show">
                <li v-for="(item,_index) in cityInfo" @click="getData(_index)">{{item.name}}</li>
            </ol>
            <p class="btn-sub">
                <input type="button" value="取消" class="input" @click="cancel">
                <input type="button" value="确定" class="sure input"  @click="sure">
            </p>
        </div>
    </div>
</template>
<script>
    import axios from "axios"
    export default {
        name:"selectedCity",
        data(){
            return {
                sendJson:{
                    lastProvince:"",
                    lastCity:"",
                    lastDistrict:"",
                },
                cityInfo:{},
                pro_id:"",
                city:"",
                city_id:"",
                district:"",
                currentArr:[],
                copy_city:"",
                copy_district:""

            }
        },
        props:[
            "identification"
        ],
        methods:{
            getData(index){
//                if(this.identification){
//                    this.changePro();
//                }
                var city_ok;
                let str=JSON.stringify(this.cityInfo[index]);
                if(str.indexOf("ProSort")!= -1){       //   点击省份后效果
                    this.pro_id=this.cityInfo[index].ProID;   //  获得点击的省份对应的id
                    let currentProvince=document.querySelectorAll(".selectedArea li")[1];
                    currentProvince.classList.add("active");
                    city_ok=true;            //   确定此时在省份中选择了一个进入到城市选择阶段
                    var _that=this;
                    _that.currentArr=[];
                    base(_that,this.city,this.pro_id,city_ok);   //  点击省份传入所有市数据去做筛选

                }
                if(str.indexOf("CitySort")!= -1){         //   点击城市后效果
                    this.city_id=this.cityInfo[index].CityID;    //   获得点击时城市对应的id
                    let currentProvince=document.querySelectorAll(".selectedArea li")[2];
                    currentProvince.classList.add("active");
                    this.sendJson.lastCity=this.cityInfo[index].name;     //  展示区域城市名字
                    city_ok=false;                              //  城市选择阶段的标志
                    _that=this;
                    _that.currentArr=[];
                    base(_that,this.district,this.city_id,city_ok)
                }
                if(str.indexOf("DisSort")!= -1){         //   点击县城后效果
                    let currentDis=document.querySelectorAll(".selectedArea li")[3];
                    currentDis.classList.add("active");
                    this.sendJson.lastDistrict=this.cityInfo[index].name;     //  展示区域城市名字
                }

                function base(_that,area,sel,city_ok){
                    let len=area.length;
                    for(let i=0;i<len;i++){
                        if(city_ok){      //   表示现在点击的时省份
                            if(area[i].ProID==sel){
                                _that.currentArr.push(area[i]);
                                _that.sendJson.lastProvince=_that.cityInfo[index].name;
                                _that.copy_city=_that.currentArr;
                            }
                        }
                        if(!city_ok){     //   表示现在点击的时从城市
                            if(area[i].CityID==sel){
                                _that.currentArr.push(area[i]);
                                _that.sendJson.lastCity=_that.cityInfo[index].name;
                                _that.copy_district=_that.currentArr;
                            }
                        }
                    }
                }
                this.cityInfo=this.currentArr;
            },
            changePro(){
                let currentProvince=document.querySelectorAll(".selectedArea li")[1];
                let currentCity=document.querySelectorAll(".selectedArea li")[2];
                let currentDis=document.querySelectorAll(".selectedArea li")[3];
                currentProvince.classList.remove("active");
                currentCity.classList.remove("active");
                currentDis.classList.remove("active");
                this.cityInfo=this.province;
                this.sendJson.lastProvince="";
                this.sendJson.lastCity="";
                this.sendJson.lastDistrict="";
            },
            changeCit(){
                let currentDis=document.querySelectorAll(".selectedArea li")[3];
                currentDis.classList.remove("active");
                this.cityInfo=this.copy_district;
                this.sendJson.lastDistrict="";
            },
            cancel(){
                this.$emit("cancel",true)
            },
            sure(){
                let str=this.sendJson.lastProvince+"/"+this.sendJson.lastCity+"/"+this.sendJson.lastDistrict;
                this.$emit("sure",str);
            }
        },
        mounted(){
            let that=this;
            axios.get("../../static/json/full.json")
                .then(function(res){
                    that.cityInfo=res.data.province;
                    that.province=res.data.province;
                    that.city=res.data.city;
                    that.district=res.data.district;
                }).catch(function (res) {
                console.log("data error")
            })
        }
    }
</script>
<style lang="scss" scoped>
    #selectedCity{
        position: absolute;
        left:50%;
        top:50%;
        transform:translate(-50%,-50%);
        z-index:4;
        width:600px;
        height: 455px;
        border:1px solid #0090d4;
        background-color: rgba(0,0,0,0.8);
        font-weight:600;
        .title{
            height: 40px;
            width: 100%;
            background-color: #003762;
            line-height: 40px;
            text-indent: 20px;
        }
        .area-content{
            padding:14px 25px 10px 25px;
        }
        .selectedArea{
            overflow: hidden;
            border-bottom:1px solid #006783;
            li{
                width: 100px;
                text-align: center;
                height:40px;
                line-height: 40px;
                float:left;
                cursor: pointer;
            }
            li:hover{
                color:#34f8d1;
                border-color:#34f8d1;
            }
            li:not(:first-child){
                margin-left: 4px;
            }
            .all{
                border-bottom:3px solid #00cbfe;
            }
            .active{
                color:#00cbfe;
                border-bottom:3px solid #00cbfe;
            }
        }
        .area-show{
            box-sizing: border-box;
            padding:14px;
            border-bottom:1px solid #006783;
            overflow: hidden;
            height:280px;
            li{
                float:left;
                box-sizing: border-box;
                text-align: center;
                line-height: 26px;
                width: 64px;
                height: 26px;
                margin-right:33px;
                margin-top:10px;
                text-align: center;
                cursor: pointer;
                border:1px solid transparent;
            }
            li:hover{
                background-color: #001925;
                border-radius: 5px;
                border:1px solid transparent;
                border-image: -webkit-gradient(linear, 0 0, 100% 0, from(#0376db), color-stop(20%, rgba(0,0,0,0)), color-stop(80%, rgba(0,0,0,0)) , to(#0376db)) 3 3;
            }
        }
        .btn-sub{
            padding:16px 100px;
            text-align: center;
            .input{
                border:none;
                background: transparent;
                color:#03e4ff;
                font-size: 14px;
                padding: 6px 28px;
                cursor: pointer;
                outline: none;
            }
            .input:hover{
                color:#05a6bc;
            }
            .sure{
                background-image:linear-gradient(to top,#0064a9,#00294d);
                background-image:-webkit-linear-gradient(to top,#0064a9,#00294d);
                background-image:-moz-linear-gradient(to top,#0064a9,#00294d);
                background-image:-ms-linear-gradient(to top,#0064a9,#00294d);
            }
        }

    }
</style>


