<template>
  <div id="app" :class="['app',isBlurMsk]">
      <div class="main">
          <myMain @getMsk="getMsk" @changeContent="changeContent" :text="sendText"></myMain>
      </div>

      <!--遮罩层部分-->
      <div class="msk" @click="mskChange">
      </div>

      <!--遮罩内容部分-->
      <div class="mskContent">
          <mskContent v-show="partShow" class="fade" :indx="which_slot._index"></mskContent>
          <searchResult v-show="searchResult" :mes="searchText" class="fade" @getDetial="getDetial"></searchResult>
          <company-details v-show="companyDetial" class="fade" :detial="companyDetial"></company-details>
          <exampleList v-show="exm_data.data" :current="exm_data.count" class="fade"></exampleList>
      </div>
  </div>
</template>

<script>
    import myMain from "./pages/boot-page/myMain.vue"
    import mskContent from "./components/mskContent.vue"
    import searchResult from './components/searchDetial/searchResult.vue'
    import companyDetails from "./components/Company-details/companyDetails.vue"
    import exampleList from './components/example/example.vue'
  export default {
    name: 'app',
    data(){
      return {
        isBlurMsk:'',
          msk:false,
          which_slot:{
            which_s:"",
              _index:""
          },
          searchText:"",
          sendText:"",
          searchResult:false,
          defaultContent:true,
          companyDetial:false,
          partShow:false,
          exm_data:{
            data:false,
            count:0
          },
          administration:"",
      }
    },
    components:{
        myMain,
        searchResult,
        companyDetails,
        exampleList,
        mskContent
    },
    created(){
         app = this;
    },
    methods:{
      mskChange(){
         this.isBlurMsk = this.isBlurMsk?'':'isMsk';
         this.searchResult=false;
         this.partShow=false;
          this.companyDetial=false;
          this.exm_data.data=false;
      },
        administrationName(msg){
            this.administration=msg;
            console.log(this.administration)
        },
        getDetial(msg){
            this.searchResult=false;
            this.isBlurMsk='';
           this.sendText =msg;
        },
        getMsk(msg){
          if(msg){
              this.isBlurMsk='isMsk';
              this.searchResult=msg.msk;
              this.searchText=msg.text;
          }
        },
        changeContent(msg){
            this.defaultContent=msg;
        },
        comDetial(msg){
            this.companyDetial=msg;
            this.isBlurMsk='isMsk';
        },
        whichSlot(msg){
            let examples=document.querySelectorAll(".big");
            let special=document.querySelector(".com-title");
            this.which_slot.which_s=msg.big;
            this.which_slot._index=msg.index;
            if(msg.index==4){
                special.innerText="舆论信息";
            }else{
                special.innerText="公司信息";
            }
            for(var i=0;i<examples.length;i++){
                examples[i].style.display="none"
            }
            examples[msg.index].style.display="block"
            this.partShow=true;
            this.isBlurMsk='isMsk';

        },
        exm(msg){
            this.exm_data.count=msg.cur;
            this.exm_data.data=msg.tar;
            this.isBlurMsk='isMsk';
        }
    }
  }
</script>
<style lang="scss" scoped>
    @import "./assets/css/app.scss";
</style>



