<template>
    <div id="search-result">
        <div class="msk_small" v-show="selectArea" @click="areaSelected"></div>
        <p class="title">找到<span>200</span>个<i class="search-main">{{mes}}</i>，请筛选</p>
        <div class="toggle">
            <ul class="search-condition">
                <li><span>企业关键字</span><input type="text" placeholder="企业关键字" v-model="enKeywords"></li>
                <li><span>其他高管姓名</span><input type="text" placeholder="其他高管姓名" v-model="enAdministration"></li>
                <li><span>地区</span><input type="text" placeholder="地区" class="area" @click="areaSelected" :value="sonJson"></li>
                <li><button class="determine" @click="getJson">确定</button></li>
            </ul>
            <div class="detialList">
                <div class="table-content">
                    <table border="0">
                        <thead>
                        <tr>
                            <th>姓名</th>
                            <th>职务</th>
                            <th>企业名称</th>
                            <th>省</th>
                            <th>市</th>
                            <th>县</th>
                            <th>企业地址</th>
                        </tr>
                        </thead>
                        <tbody class="sro">
                        <tr v-for="(item ,index) in searchList" @click="getDetial(index)">
                            <td>{{item.name}}</td>
                            <td>{{item.station}}</td>
                            <td>{{item.enterpriseName}}</td>
                            <td>{{item.province}}</td>
                            <td>{{item.city}}</td>
                            <td>{{item.district}}</td>
                            <td>{{item.adress}}</td>
                        </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        <Jpagination  @pageChange="pageChange" :initTotalCount="total"></Jpagination>
        <selected-city v-show="selectArea" @sure="area" @cancel="area1" :identification="selectArea"></selected-city>
    </div>
</template>
<style lang="scss" scoped>
    .msk_small{
        position: fixed;
        width:100%;
        height:100%;
        background:rgba(0,0,0,0.6);
        left:0;
        top:0;
        z-index:2;
    }
    .address{
        z-index:4;
        width: 600px;
        height: 460px;
        position: absolute;
        top: 0;
        .address-header{
            background-color:#00090f;
            padding-top:70px;
            border-bottom:1px solid #006783;
            ul{
                li{
                    color:#fff !important;
                    border-bottom:#fff solid 3px;
                    float:left;
                    padding:0 10px 10px 10px;
                }
            }
        }
        .address-container{
            height:460px;
            background-color:#00090f;
            ul{
                height: 280px;
                border-bottom:1px solid #006783;
                li{
                    float:left;
                    border:none;
                    color:#fff;
                }
            }
        }
    }
    #search-result{
        position:absolute;
        left:50%;
        top:50%;
        transform:translate(-50%,-50%);
        background:rgba(0,0,0,0.4);
        z-index:2;
        color:#fff;
        font-size: 14px;
        width: 956px;
        height: 600px;
        box-shadow: 0px 0px 60px rgba(8,56,77,0.8) inset;
        border:1px solid #001721;
        padding:5px;
        border:1px solid rgba(38,55,65,0.6);
        .pagination{
            position: absolute;
            right:10px;
            bottom:16px
        }
        .search-main{
            color:#f25e3d;
        }
        .toggle{
            position: relative;
        }
        .title{
            width: 100%;
            height:40px;
            line-height:40px;
            text-indent:20px;
            background-color: #02284d;
            z-index:3;
        }
        .search-condition{
            padding:20px 16px;
            ul{
                overflow: hidden;
            }
            li:not(:last-child){
                float: left;
                margin-right:30px;
            }
            input{
                border:1px solid #0695cd;
                padding:10px 0px 10px 10px;
                margin-left:10px;
                width: 168px;
                background:transparent;
                color:#fff;
            }
                input:focus{
                    outline:none;
                    box-shadow:0 0 10px rgba(69,190,245,0.8); //向外漫射程度1
                    -webkit-box-shadow:0 0 10px rgba(69,190,245,0.8);//向外漫射程度3
                }

            .area{
                width: 126px;
            }
            .determine{
                cursor: pointer;
                width: 80px;
                height: 30px;
                outline: none;
                line-height: 30px;
                border:none;
                color:#09e2f1;
                background:-moz-linear-gradient( to bottom,rgba(0,102,179,0.8),rgba(0,45,77,0.8));
                background:-webkit-linear-gradient( to bottom,rgba(0,102,179,0.8),rgba(0,45,77,0.8));
                background:linear-gradient( to bottom,rgba(0,102,179,0.8),rgba(0,45,77,0.8));
            }
            .determine:hover{
               color:#09cde6;
            }
        }
        .detialList{
            width:100%;
            height:490px;
            box-shadow: 0px 0px 60px rgba(6,49,67,0.6) inset;
            background: rgba(1,18,28,0.8);
            .table-content{
                overflow: hidden;
                height:450px;
            }
            table{
                width: 100%;
                table-layout:fixed;
                text-align: center;
                overflow-y:auto;
            }
            tr{
                width:100%;
                height: 40px;
            }
            thead{
                width:100%;
                background-color: #002840;
                th{width:20%}
            }
            .sro{
                overflow-y: auto;
                width: 828px;
                height: 398px;
            }
            .sro::-webkit-scrollbar-track{
                border-radius: 10px;
                background-color: #051c26;
            }
            .sro::-webkit-scrollbar-thumb {
                border-radius: 10px;
                width: 8px;
                -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
                background-color:#026086;
            }
            tbody{
                width:100%;
                tr{
                    border:1px solid transparent;
                }
                 tr:after{
                  content: "";
                  display: block;
                 position: absolute;
                  left: -50%;
                   width: 200%;
                  height: 1px;
                  background:#263741;
                 -webkit-transform:scale(0.5);
                 }
                td{
                    height:40px;
                    overflow: hidden;
                    text-overflow:ellipsis;
                    white-space: nowrap;
                }
                tr:hover{
                    background:rgba(19,88,132,0.4);
                    box-shadow:0 0 20px rgba(34,101,144,0.6) inset;
                    border:1px solid #195e9f;
                    cursor: pointer;
                }
            }


        }
    }
</style>
<script>
    import selectedCity from "./selectedCity.vue"
    import Jpagination from "../test/pagination.vue"
    import axios from 'axios'
    export default{
        name:'search-result',
        data(){
            return {
                "searchList":[],
                selectArea:false,
                sonJson:'',
                enKeywords:'',
                enAdministration:'',
                total: 1,
                display: 10,
                current: 1,
                pageSize:10,
                transfer:""
            }
        },
        props:["mes"],
        components: {
            selectedCity,
            Jpagination
        },
        methods:{
            getDetial(index){
               let text= this.transfer[index].name;
               app.getDetial(text)
            },
            pageChange(pager) {
                this.current = pager;
            },
            areaSelected(){
                this.selectArea=!this.selectArea;
            },
            area(msg){
                this.sonJson=msg;
            },
            area1(msg){
                this.sonJson=msg;
            },
            getJson(){
                let that=this;
                axios.get("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/kinship/enterprise/searchScreen",
                    {
                        params:{
                            userScreen:that.mes,
                            enKeywords:that.enKeywords,
                            enAdministration:that.enAdministration,
                            area:that.sonJson,
                            pageSize:10,
                            page:that.current
                        }
                    }).then(function(res){
                        let data=res.data.data;
                        that.total=data.totalRecord;
                    that.searchList=data.searchList;

                }).catch(function(res){
                    console.log("data error")
                })
            }
        },
        watch:{
            sonJson(newValue,oldValue){
                if(newValue!=oldValue){
                    this.selectArea=false;
                    if(newValue==true){
                        this.sonJson="";
                    }
                }
             },
            current(newValue,oldValue){
                    if(newValue!=oldValue){
                        this.getJson(this.current)
                    }
            },
            mes(newValue,oldValue){
                if(newValue!=oldValue){
                    this.searchList=[];
                    this.total=1;
                    this.current=1;
                }
            }
        },
    }
</script>


