<template>
    <div>
        <div class="main-content">
            <span class="link-box" @click="showExample">
                <img src="../../assets/img/spr-contact.png">
            </span>
            <div class="img-box">
                <span class="img-item">
                    <span class="title">挖掘人之间关系</span>
                </span>
                <span class="img-item">
                   <span class="title">预警潜在风险</span>
                </span>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'dMain',
        data(){
            return {
                obj2:{
                    "tar":true,
                    "cur":0
                }
            };
        },
        methods:{
            showExample(){
                this.obj2.cur++;
                app.exm(this.obj2);
            },
        }
    }
</script>
<style lang="scss" scoped>
    @import "./../../assets/css/_variable.scss";
    @import "./../../assets/css/_mixin.scss";
    .main-content{
        overflow: hidden;
        position:relative;
        width:900px;
        height:820px;
        background:rgba(7,20,32,0.5);
        float:right;
        span.link-box{
            position: absolute;
            top:30px;
            right:90px;
            cursor: pointer;
            img:hover{
                border-radius: 50%;
                box-shadow:0 0 24px #2afefb ;
            }
        }
        .img-box{
            width: 100%;
            height: 267px;
            margin: 24% auto 0 auto;
            text-align: center;
        }
        .img-box .img-item{
            display: inline-block;
            width: 344px;
            height: 277px;
            margin: 0 3%;
            background-position: center center;
            background-size: cover;
        }
        .img-box .img-item:first-child{
            background-image:url(../../assets/img/spr-excavate.png);
        }
        .img-box .img-item:last-child{
            background-image:url(../../assets/img/spr-potential.png);
        }
        .img-box .title{
            display: inline-block;
            position: relative;
            top: 286px;
            width: 100%;
            text-align: center;
            font-size: 14px;
            color: #00eaff;
        }
    }
</style>



