<template>
   <div id="mskContent">
       <p class="title com-title">公司信息</p>
       <part-detial>
           <template slot="part1" scope="props">
               <div class="example big">
                   <div class="content" v-for="item in litigation">
                       <p class="typ" v-text="item.typ"></p>
                       <ul class="theme">
                           <li v-text="item.enName"></li>
                           <li><span v-text="item.court"> </span><i v-text="item.date"></i></li>
                       </ul>
                       <ol class="theme-content">
                           <li>裁判理由</li>
                           <li class="sys" v-html="item.reason"> </li>
                       </ol>
                       <ol class="theme-content">
                           <li>原告</li>
                           <li v-text="item.plaintiff"></li>
                       </ol>
                       <ol class="theme-content">
                           <li>被告</li>
                           <li v-text="item.defendant"></li>
                       </ol>
                   </div>
               </div>
           </template>
           <template slot="part2" scope="props">
               <div class="example big">
                   <div class="content" v-for="item in dishonesty">
                       <p class="special-name" v-html="item.enName"></p>
                       <ul class="dishonesty-content">
                           <li> <span>状态：</span><em v-text="item.state"></em></li>
                           <li><span>立案时间：</span><em v-text="item.start"></em></li>
                           <li> <span>执行法院：</span><em v-text="item.court"></em></li>
                           <li><span>发布时间：</span><em v-text="item.release"></em></li>
                           <li style="padding:0px"><span>内容：</span><em v-text="item.content"></em></li>
                           <li><span>案号：</span><em v-text="item.case"></em></li>
                       </ul>
                   </div>
               </div>
           </template>
           <template slot="part4" scope="props">
               <div class="example big">
                   <div class="content" v-for="item in change">
                       <p :class="['typ_base',item.typ1?'typ1':'typ2']" v-text="item.tpy"></p>
                       <p class="company-name" v-text="item.title">
                       </p>
                       <ul class="corporate-ul">
                           <li> <span>变更前：</span><em v-text="item.before"></em></li>
                           <li><span>处罚决定日期：</span><em v-text="item.date"></em></li>
                           <li> <span>变更后：</span><em v-text="item.after"></em></li>
                       </ul>
                   </div>
               </div>
           </template>
           <template slot="part3" scope="props">
               <div class="example big ">
                   <div class="content" v-for="item in punish">
                       <p class="typ" v-text="item.typ"></p>
                       <p class="company-name" v-text="item.enName">济南新泰鑫电子商务有限公司 </p>
                       <ul class="Indictment-content">
                           <li> <span>文号：</span><em v-text="item.case"></em></li>
                           <li><span>决定机关名称：</span><em v-text="item.court"></em></li>
                           <li> <span>违法行为类型：</span><em v-text="item.punishType"></em></li>
                           <li><span>处罚决定日期：</span><em v-text="item.date"></em></li>
                           <li><span>行政处罚内容：</span><em v-text="item.content"></em></li>
                       </ul>
                   </div>
               </div>
           </template>
           <template slot="part5" scope="props">
               <div class="example  big">
                   <div class="content" v-for="item in negative">
                   <p class="change" v-text="item.title">新闻标题一 </p>
                       <ul class="dishonesty-content">
                           <li> <span>来源：</span><em v-text="item.source"></em></li>
                           <li><span>日期：</span><em v-text="item.date"></em></li>
                           <li><span>摘要：</span><em v-text="item.abstract"></em></li>
                       </ul>
                   </div>
               </div>
           </template>
       </part-detial>
   </div>
</template>
<script>
    import partDetial from "./Company-details/part-detial.vue"
    import axios from "axios"
    import '../assets/css/partDetial.scss'
    export default {
        data(){
            return {
                typ1:false,
                _index:"",
                "litigation":[
                    {"typ":"民事案件",
                        "enName":"快乐米（北京）信息技术有限公司与小米科技有限责任公司侵害商标权纠纷2015知民初821一审民事裁定书",
                        "court":"宁夏回族自治区高级人民法院",
                        "date":"2016-03-08",
                        "reason":"裁定如下：一、撤销石嘴山市中级人民法院（2015）石民知初字第2号民事判决；二、准许<em>公司名称XXXXXXX</em>有限责任公司撤 回对大武口区永兴达通讯设备经销部的起诉。一审案件受理费625元，减半收取313元；二审案件受理费598元减半收取299元，均由小米科技有限责任公司负……",
                        "plaintiff":"快乐米（北京）信息技术有限公司  ",
                        "defendant":"公司名称XXXXXXX"},
                    {"typ":"民事案件",
                        "enName":"快乐米（北京）信息技术有限公司与小米科技有限责任公司侵害商标权纠纷2015知民初821一审民事裁定书",
                        "court":"宁夏回族自治区高级人民法院",
                        "date":"2016-03-08",
                        "reason":"裁定如下：一、撤销石嘴山市中级人民法院（2015）石民知初字第2号民事判决；二、准许<em>公司名称XXXXXXX</em>有限责任公司撤 回对大武口区永兴达通讯设备经销部的起诉。一审案件受理费625元，减半收取313元；二审案件受理费598元减半收取299元，均由小米科技有限责任公司负……",
                        "plaintiff":"快乐米（北京）信息技术有限公司  ",
                        "defendant":"公司名称XXXXXXX"}
                ],
                "dishonesty":[
                    {"enName":"广州<i>小米</i>包装制品有限公司",
                        "state":"全部未履行",
                        "start":"2016-03-08",
                        "court":"东莞市第一人民法院",
                        "release":"2016-03-08",
                        "content":"xxxxxxxx",
                        "case":"（2016）云2801执1069号"
                    }
                ],
                "punish":[
                    {
                        "typ":"工商行政处罚",
                        "enName":"济南新泰鑫电子商务有限公司",
                        "case":"济南市中区监管处字 [2016] 31号",
                        "court":"济南市市中区市场监督管理局",
                        "punishType":"其它广告经营违法行为",
                        "date":"2016-03-08",
                        "content":"责令消除影响、罚款、停止广告发布"
                    }
                ],
                "change":[
                    {
                        "tpy":"法人变更",
                        "title":"快乐米（北京）信息技术有限公司与小米科技有限责任公司侵害商标权纠纷2015知民初821一审民事裁定书",
                        "before":"张三",
                        "date":"2016-03-08",
                        "after":"李四",
                        "typ1":true
                    },
                    {
                        "tpy":"资产变更",
                        "title":"快乐米（北京）信息技术有限公司与小米科技有限责任公司侵害商标权纠纷2015知民初821一审民事裁定书",
                        "before":"张三",
                        "date":"2016-03-08",
                        "after":"李四",
                        "typ1":false
                    }
                ],
                "negative":[
                    {
                        "title":"新闻标题一",
                        "source":"全部未履行",
                        "date":"2016-03-08",
                        "abstract":"xxxxxx"
                    }
                ]
            }
        },
        components:{
            partDetial
       },
        methods:{
            getJson(index){
                switch(index)
                {
                    case 0:
                        request("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/cloudMap/enterprise/litigation")
                        break;
                    case 1:
                        request("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/cloudMap/enterprise/dishonesty")
                        break;
                    case 2:
                        request("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/cloud/enterprise/punish")
                        break;
                    case 3:
                        request("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/cloudMap/enterprise/change")
                        break;
                    case 4:
                        request("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/cloudMap/enterprise/negative")
                        break;
                    default:
                        console.log(index)
                }
                let that=this;
                function request(url){
                    axios.get(url)
                        .then(function(res){
                            let data=res.data.data;
                            if(data.litigation){
                                that.litigation=data.litigation;
                            }else if(data.dishonesty){
                                that.dishonesty=data.dishonesty;
                            }else if(data.punish){
                                that.punish=data.punish;
                            }else if(data.change){
                                that.change=data.change;
                            }else if(data.negative){
                                that.negative=data.negative;
                            }
                        }).catch(function(res){
                        console.log("data error")
                    })
                }
            }
        },
        props:["indx"],
        created(){
            this.getJson(0);
        },
        watch:{
            indx:function(newValue,oldValue){
                if(newValue!=oldValue){
                    this._index=newValue;
                    this.getJson(this._index);
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
    .title{
        height: 40px;
        background-color: #012a4d;
        text-indent: 20px;
        line-height: 40px;
    }
#mskContent{
    position: absolute;
    padding: 5px;
    z-index: 2;
    width: 826px;
    height: 444px;
    background-color: rgba(1, 19, 27, 0.7);
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    overflow: hidden;
    box-shadow: 0px 0px 40px #12526e inset;
}
</style>


